.ContentCard {
  margin: 16px;
  height: 88px;
  display: -ms-flexbox;
  display: flex; }
  .ContentCard-img {
    height: 88px;
    width: 94px;
    border-radius: 5px;
    border: 1px solid #4a4a4a2d;
    padding: 10px, 0; }
